@use "../setting" as *;
@use "../mixin" as *;
@use "../color" as *;
@use "../variable" as *;

.about {
  &__wrapper {
    background: #fff;
    border-radius: 10px;
  }

  &__title {
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;

    @include mq() {
      flex-direction: column;
    }
  }
  &__card {
    @include mq("pc") {
      width: calc($w100 / 3 - 36px);
      margin: 0 16px;
      padding: 10px 20px;
    }
    @include mq("tab") {
      width: calc($w100 / 2 - 28px);
      margin: 8px 12px;
      padding: 10px 16px;
    }
    @include mq("md") {
      width: $w100;
      margin: 0;
      padding: 10px;
      margin-top: 0.8rem;
    }
    img {
    }

    &__title {
      margin: 15px 0;
      text-align: center;
      padding-bottom: 16px;
      position: relative;
      &::after {
        position: absolute;
        content: "";
        width: $w100;
        height: 2px;
        background: red;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &__text {
    }
  }
}
